<template>
  <v-container >
    <v-row class="mt-6" >
      <v-col cols="12" sm="12" lg="12">
        <v-skeleton-loader
         
          type="card"
        >
          <v-card class="mx-auto ">
            <v-card-title class="blue-grey white--text">
              <span class="">   </span>
              <v-spacer></v-spacer>
              
              
 <v-dialog
      v-model="dialog"
      persistent
      max-width="600px"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn key="bt1"
          color="primary"
          dark
          v-bind="attrs"
          v-on="on"
        >
           <v-icon>mdi-plus</v-icon>
        </v-btn>
      </template>
      <v-card>
        <v-card-title>
          <span class="text-h5"> </span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              
              <v-col cols="12">
                <v-text-field v-model="title" key="tx1"
                  label=""
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field v-model="desc" key="tx2"
                  label=""
                                   required
                ></v-text-field>
              </v-col>
             
               
            </v-row>
          </v-container>
       
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn key="bt2"
            color="blue darken-1"
            text
            @click="dialog = false"
          >
            
          </v-btn>
          <v-btn key="bt3"
            color="blue darken-1"
            text
            @click="addAds"
          >
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
            </v-card-title>
            <v-card-text class="py-0" >
              <v-timeline dense>
                <v-slide-x-reverse-transition group hide-on-leave>
                  <v-timeline-item
                    v-for="(item,index) in adslist"
                    :key="item.key"
                    color="blue-grey"
                    small
                    pr-0
                  >
                    <v-alert 
                      color="blue-grey lighten-5"
                      icon="mdi-bullhorn-outline "
                      class=""
                    >
                      <v-row>
                        <h2 class="blue-grey--text text--darken-2 pt-4 pr-4">
                          {{ item.title }}
                        </h2>
                        <v-spacer></v-spacer>
     <v-dialog 
      v-model="dialog2"
      persistent
      max-width="600px" :retain-focus="false"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn key="bt4"
          class="mx-2" fab dark small color="green lighten-3"
          v-bind="attrs"
          v-on="on"
          @click="getAds(index)"
        >
         <v-icon dark> mdi-pencil </v-icon>
        </v-btn>
      </template>
      <v-card>
        <v-card-title>
          <span class="text-h5"> </span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              
              <v-col cols="12">
                <v-text-field v-model="title" key="tx4"
                  label=""
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field v-model="desc" key="tx5"
                  label=""
                                   required
                ></v-text-field>
              </v-col>
             
               
            </v-row>
          </v-container>
       
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn key="bt5"
            color="blue darken-1"
            text
            @click="cancl"
          >
            
          </v-btn>
          <v-btn key="bt6"
            color="blue darken-1"
            text
            @click="edtAds(item.key)"
          >
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

                        <v-btn class="mx-2" fab dark small color="red lighten-3" @click="deltAds(item.key)" key="bt7"> 
                          <v-icon dark> mdi-delete </v-icon>
                        </v-btn>
                      </v-row>
                      <v-divider class="blue-grey my-2 mt-5"></v-divider>
                      <p class="blue-grey--text">
                        {{ item.desc }}
                      </p>
                    </v-alert>
                  </v-timeline-item>
                </v-slide-x-reverse-transition>
              </v-timeline>
            </v-card-text>
          </v-card>
        </v-skeleton-loader>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
/* eslint-disable */
import Axios from "axios";
import {db} from "@/fb"
import { collection, getDocs, onSnapshot,doc ,deleteDoc ,setDoc } from "firebase/firestore";
import Swal from "sweetalert2";

export default {
  name: "adsList",
  data() {
    return {
      dialog2:false,
      dialog: false,
      adslist: [],
      errors: [],
      title:"",
      desc:"",
    };
  },
  mounted() {

  
 onSnapshot(collection(db, 'adslist'), (querySnapshot) => {
  this.adslist = [];
  querySnapshot.forEach((doc) => {
      this.adslist.push({
          key: doc.id,
          title: doc.data().adstitle,
          desc: doc.data().adsdesc,
         
        });
  });
 
});

  },
  activated() {},
  computed: {},
   created () {
    

    
  },

  methods: {
 deltAds(id){

  Swal.fire({
  title: '  ',
  icon: 'question',
  iconHtml: '',
  confirmButtonText: '',
  cancelButtonText: '',
  showCancelButton: true,
  showCloseButton: true,
    preConfirm:()=>{
              deleteDoc(doc(collection(db, 'adslist'),id));
            }
})

  
 },
  async addAds(){
  if(this.title===""||this.desc===""){
     Swal.fire({
            title: "        ",
            text: "",
            icon: "error",
            confirmButtonText: "",
          
          });
  }
  else{
  this.dialog = false;
  await setDoc(doc(collection(db, 'adslist')), {
  adstitle: this.title,
  adsdesc: this.desc,
  });
}
  this.title=""
  this.desc=""
 },
  async edtAds(id){
  this.dialog2 = false;
  await setDoc(doc(collection(db, 'adslist'),id), {
  adstitle: this.title,
  adsdesc: this.desc,
  });
  this.title=""
  this.desc=""
 },
 getAds(id){
        this.title= this.adslist[id].title; 
        this.desc= this.adslist[id].desc;      
 },
 cancl(){
  this.title=""
  this.desc=""
  this.dialog2=false
 },
 
  },
};
</script>

<style lang="scss"></style>
